let currentAudio = "";
class Song extends React.Component {
  constructor(props) {
    //id, title, preview, artist
    super(props);
    this.state = {
      audio: false
    };
  }
  clickHandler = e => {
    e.preventDefault();
    const audio = e.currentTarget.nextElementSibling;
    if (this.state.audio) {
      this.setState({
        audio: false
      });
      audio.pause();
    } else {
      if (currentAudio) {
        const preAudio = document.querySelector(`#${currentAudio}`);
        if (preAudio) {
          preAudio.pause();
        }
      }
      currentAudio = audio.id;
      this.setState({
        audio: true
      });
      audio.play();
      audio.addEventListener("ended", this.stopHandler);
      audio.addEventListener("pause", this.stopHandler);
    }
  };
  stopHandler = e => {
    e.preventDefault();
    this.setState({
      hover: false,
      audio: false
    });
  };

  render() {
    const { props } = this;
    return (
      <Container>
        <Poster id={props.id} poster={props.artist.picture}></Poster>
        <Title>{props.title}</Title>
        <Singer>{props.artist.name}</Singer>
        <Preview>
          <Button onClick={this.clickHandler}>
            <span role="img" aria-label="img">
              🎼
            </span>
          </Button>
          <audio id={`audioSong${props.id}`}>
            <source src={props.preview}></source>
          </audio>
        </Preview>
      </Container>
    );
  }
}

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  align-items: center;
  margin: 30px;
  /* background-color:lightblue; */
`;
const Poster = styled.div`
  width: 70px;
  height: 100px;
  background-image: url(${props => props.poster});
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
`;

const Title = styled.span`
/* background-color:yellow; */
`;
const Singer = styled.span`
/* background-color:pink; */
`;
const Preview = styled.div`
/* background-color:black; */
`;
const Button = styled.button`

  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: 0;
  cursor: pointer;
  font-size: 1.4rem;
  &:hover {
    transform: scale(1.1);
  }
  &:active {
    transform: scale(1);
  }
`;
export default Song;
