import React from "react";
import styled, { createGlobalStyle } from "styled-components";
import img from "../image/1.jpg";
import { Link } from "react-router-dom";

createGlobalStyle`
    html,body{
        height:100%;
        width:100%;
    }
`;
const Container = styled.div`
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
`;
const Header = styled.div`
  width: 100%;
  /* height: 200px; */
  background: #333 url(${img}) no-repeat center center/cover;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const Title = styled.h1`
  font-size: 32px;
  color: white;
`;
const Search = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 50px;
  font-size: 1.2rem;
  font-weight: 500px;
  border-radius: 30px;
  border: 3px solid rgba(24, 21, 50, 0.55);
  outline: none;
  padding: 0 1.5rem;
  margin: 20px 0;
  color: #333;
  background-color: rgba(250, 250, 250, 0.55);
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.17);
`;
const SearchBar = styled.input`
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: 0;
  width: 90%;
`;
const Button = styled.button`
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  width: 100%;
  border: 0;
  outline: 0;
  color: white;
  font-weight: border;
  &:hover {
    transform: scale(1.05);
  }
  &:active {
    transform: scale(1);
  }
`;

class Home extends React.Component {
  state = {
    input: ""
  };
  keyChangeHandler = e=>{
    // e.preventDefault();
    this.setState({
      input:e.target.value
    })
    
  }
  keyDownHandler = e=>{
    if(e.keyCode===13){
      const go = document.querySelector("#go");
      go.parentElement.click();
      e.target.value="";
    }
  }
  render() {
    return (
      <Container>
        <Header>
          <Link to='/' style={{textDecoration:'none'}}>
          <Title>FIND MUSIC . COM</Title>
          </Link>
          <Search>
            <SearchBar id="searchBar" 
            onChange={this.keyChangeHandler}
            onKeyDown = {this.keyDownHandler}
            ></SearchBar>
            <Link to={`/${this.state.input}`}>
              <Button id="go">GO</Button>
            </Link>
          </Search>
        </Header>
      </Container>
    );
  }
}

export default Home;