import React from "react";
import axios from "axios";
import styled from "styled-components";
import Reco from "./Reco";

const RECO = ["manhattans","james brown","fonsi", "eminem","jovanotti", "BTS"];
const URL = "https://deezerdevs-deezer.p.rapidapi.com/search?";

class Body extends React.Component {
  state = {
    posts: []
  };

  async componentDidMount() {
    const num = Math.floor(Math.random() * 6);
    const {
      data: { data }
    } = await axios.get(`${URL}`, {
      params: {
        q: RECO[num],
        limit: 20
      },
      method: "GET",
      headers: {
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    
    this.setState({
      posts: data,
      name:data[0].artist.name
    });
    
  }
  render() {
    return (
      <Container>
        <Recommend>
          {this.state.posts ? (
            <>
              <Title>Today's SINGER</Title>
              <SubTitle>{this.state.name}</SubTitle>
            </>
          ) : null}
          <Albums>
            {this.state.posts?.map((each,index) => (
              <Reco
                key={each.id}
                id={index}
                title={each.title}
                preview={each.preview}
                album={each.album}
              ></Reco>
            ))}
          </Albums>
        </Recommend>
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
`;
const Recommend = styled.div`
  padding: 70px 0;
  width: 100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
`;
const Title = styled.h1`
  font-size: 2rem;
  margin:30px;  
  padding:0 80px;
  align-self:flex-start;
`;
const SubTitle = styled.h1`
  font-size: 1.5rem;
  margin:30px;
  padding:0 100px;
  align-self:flex-start;
`;
const Albums = styled.div`
  width: 80%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 20px;
  justify-items: center;
  overflow-x:auto;
  overflow-y:hidden;
`;
export default Body;
